<template>
  <div id="app">
    <img src="./assets/logo.png">
    <h1>{{ msg }}</h1>
    <input type="text" v-model="val" name="bookIndex" placeholder="  书名、作者、ISBN"> 
    <button v-on:click="searcFn(val)"> 搜索 </button>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      msg: '《*\\ 豆瓣书城 /*》',
      val: ''
    }
  },
  methods:{
    searcFn(par){
      par = this.val || '南极';
      if(par == '南极'){
        this.$router.push({path:'/booklist/南极'});
      }else{
        this.$router.push({path: '/bookinfo/'+par})
      }
    }
  },
  created(){
    this.$router.push({path:'/booklist/南极'});
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



